<template>
    <div id="app" class="raiz">
        <div class="vis-header">
            <h4>Interactivo: en América Latina y el Caribe la tasa de embarazos adolescentes sigue siendo muy alta</h4>
        </div>
        <div class="vis-body">
            <div class="vis-legend">
                <svg>
                    <defs>
                        <linearGradient id="gradiente-escala" x1="0%" y1="0%" x2="100%" y2="0%">
                            <stop offset="0%" style="stop-color:RGB(250,237,229);stop-opacity:1"></stop>
                            <stop offset="16%" style="stop-color:RGB(245,200,192);stop-opacity:1"></stop>
                            <stop offset="33%" style="stop-color:RGB(239,164,180);stop-opacity:1"></stop>
                            <stop offset="50%" style="stop-color:RGB(206,69,149);stop-opacity:1"></stop>
                            <stop offset="66%" style="stop-color:RGB(206,69,149);stop-opacity:1"></stop>
                            <stop offset="84%" style="stop-color:RGB(160,36,124);stop-opacity:1"></stop>
                            <stop offset="100%" style="stop-color:RGB(113,25,116);stop-opacity:1"></stop>
                        </linearGradient>
                    </defs>
                    <g>
                        <rect id="escala" x="20" y="17" width="0" height="10" fill="url(#gradiente-escala)"></rect>
                        <circle id="escala-circle" cx="20" cy="22" r="5" fill="333" stroke-width="1px" stroke="#fff"></circle>
                    </g>
                </svg>
            </div>
            <div class="vis-content">
            </div>
        </div>
        <div class="vis-footer">
            <p>
                <small>Fuente: Naciones Unidas</small>
            </p>
        </div>
    </div>
</template>

<script>

    import * as d3 from 'd3';
    import * as topojson from 'topojson';

    export default {
        name: 'app',
        components: {},
        data: () => {
            return {
                margin: 20,
                width: 0,
                height: 0,
                legendScale: undefined
            }
        },
        mounted: function () {
            this.loadVis();
        },
        methods: {
            loadVis: function () {
                let vm = this;
                vm.width = d3.select("#app").node().getBoundingClientRect().width;
                vm.height = vm.width;
                if(vm.width > 600) {
                    vm.height = vm.width * 0.7;
                }
                this.loadAxis(vm);
                this.loadMap(vm);
            },
            loadAxis: (vm) => {

                d3.select("#escala").attr("width", (vm.width - (vm.margin * 2)) + "px");
                let svgLegend = d3.select("div.vis-legend > svg")
                    .attr("width", vm.width)
                    .attr("height", 45);

                let axisScale = d3.scale.linear().domain([40, 115]).range([0, vm.width - vm.margin * 2]);
                vm.legendScale = axisScale;
                let axis = d3.svg.axis().scale(axisScale).orient("bottom").tickValues([40, 60, 80, 100, 115]);
                svgLegend.append("g").attr("transform", "translate(" + vm.margin + ",27)").call(axis);
                svgLegend.append("text")
                    .attr("x", vm.margin)
                    .attr("y", 12)
                    .attr("style", "font-size: 12px;")
                    .text("Tasa de embarazo por cada 1.000 adolescentes");
            },
            loadMap: (vm) => {

                let svg = d3.select(".vis-content").append("svg")
                    .attr("width", vm.width)
                    .attr("height", vm.height);

                let features = svg.append("g")
                    .attr("class", "features");

                let color = d3.scale.linear()
                    .domain([40, 52, 64, 75, 87, 99, 110])
                    .range(["#FAEDE5", "#F5C8C0", "#EFA4B4", "#EA729F", "#CE4595", "#A0247C", "#711974"]);

                let tooltip = d3.select("body").append("div").attr("class", "tooltip");

                let centered;

                let tooltipOffset = {x: 5, y: -25};

                let projection = d3.geo.equirectangular()
                    .scale(1000)
                    .center([0, 0])
                    .translate([vm.width / 2, vm.height / 2]);

                let path = d3.geo.path()
                    .projection(projection);

                d3.json(process.env.BASE_URL + "data/embarazo_adolescente_min.topojson", function (error, geodata) {
                    if (error) return console.log(error);

                    let topology = topojson.feature(geodata, geodata.objects.collection);

                    let bounds = d3.geo.bounds(topology);
                    let center = d3.geo.centroid(topology);
                    center[0] = center[0] - 10;
                    let distance = d3.geo.distance(bounds[0], bounds[1]);
                    let scale = (vm.height / distance) * 1.2;

                    projection.scale(scale).center(center);

                    features.selectAll("path")
                        .data(topology.features)
                        .enter()
                        .append("path")
                        .attr("style", (d) => {
                            return "fill:" + color(d.properties.embarazo_adolescente_rate);
                        })
                        .attr("d", path)
                        .attr("class", "country")
                        .on("mouseover", showTooltip)
                        .on("mousemove", moveTooltip)
                        .on("mouseout", hideTooltip)
                        .on("click", clicked);
                });

                function clicked(d) {
                    let x, y, k;
                    if (d && centered !== d) {
                        let centroid = path.centroid(d);
                        let b = path.bounds(d);
                        x = centroid[0];
                        y = centroid[1];
                        k = .8 / Math.max((b[1][0] - b[0][0]) / vm.width, (b[1][1] - b[0][1]) / vm.height);
                        centered = d
                    } else {
                        x = vm.width / 2;
                        y = vm.height / 2;
                        k = 1;
                        centered = null;
                    }

                    features.selectAll("path")
                        .classed("highlighted", function (d) {
                            return d === centered;
                        })
                        .style("stroke-width", 0.5 / k + "px"); // Keep the border width constant

                    features
                        .transition()
                        .duration(750)
                        .attr("transform", "translate(" + vm.width / 2 + "," + vm.height / 2 + ")scale(" + k + ")translate(" + -x + "," + -y + ")");
                }

                function showTooltip(d) {
                    moveTooltip();
                    tooltip.style("display", "block")
                        .text(d.properties.NAME_ES + ": " + d.properties.embarazo_adolescente_rate);
                    d3.select("#escala-circle")
                        .transition()
                        .duration(300)
                        .attr("cx", vm.legendScale(d.properties.embarazo_adolescente_rate) + 20 + "px")
                }

                function moveTooltip() {
                    tooltip.style("top", (d3.event.pageY + tooltipOffset.y) + "px")
                        .style("left", (d3.event.pageX + tooltipOffset.x) + "px");
                }

                function hideTooltip() {
                    tooltip.style("display", "none");
                }
            }
        }
    }
</script>

