        -moz-osx-font-smoothing: grayscale;
        color: #222;
        max-width: 720px;
        margin: 0 auto;
        // border: 1px solid #ccc;
        padding: 5px;
    }

    div.vis-header {
        h3, h4 {
            margin-top: 0;
        }

        p {

        }

        @include mobile {
            padding: 0 10px;
        }
    }

    div.vis-body {
        min-height: 200px;
        margin-top: 30px;
    }

    div.vis-footer {
        margin-top: 10px;
        padding: 5px 0;
        border-top: 1px solid grey;

        @include mobile {
            padding: 0 10px;
        }

        p {
            margin-bottom: 0;
        }
    }

    g.tick {
        line {
            stroke: #333;
            stroke-width: 0.5px;
            fill: none;
        }

        text {
            font-size: 12px;
        }
    }

    path.domain {
        stroke: #333;
        stroke-width: 0.5px;
        fill: none;
    }

    path.country {
        stroke-width: 0.25;
        stroke: #fff;

        &:hover {
            cursor: pointer;
        }
    }

    div.tooltip {
        position: absolute;
        background-color: white;
        border: 1px solid black;
        color: black;
        font-weight: bold;
        padding: 4px 8px;
        display: none;
    }


</style>
